body {
  //   font-family: var(--primary-font);
  font-weight: 300;
  font-size: 16px;
  line-height: 1;
  @media screen and (min-width: 1444px) {
    font-size: 18px;
  }
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  display: block;
}
